/*================= 3.3 HOME V3 ==================*/

/*=== 3.3.1 DEAL SECTION ===*/

.dealSlider{
	margin-bottom:80px;
	.imageBox{
		border:1px solid #d5d5d5;
		margin: 1px;
	}
	.productDeal{
		padding:20px 15px;
		h3{
		   font-size: 20px;
		   font-weight: 400;
		   margin: 0;
		   text-transform: capitalize;
		   display: inline-block;
		   @include tablet {
		    	font-size: 15px;
		    }
		    @include desktops {
		    	font-size: 20px;
		    }
		   span{
		   	color: $primary-color;
		   	font-weight: 700;
		   }
		}
		.rating{
			display: inline-block;
			float: right;
			color: #d5d5d5;
			@include transition(all 0.3s ease);
		}
	}
	.productCaption{
		h3{
		   font-size: 20px;
		   font-weight: 400;
		   margin-bottom: 20px;
		   text-transform: capitalize;
		   a{
		   		color: $secondary-color;
		   }
		}
		.btn-border{
			padding-left:25px;
    		padding-right:25px;
			border-color:$primary-color;
			margin-bottom: 50px;
			border: 2px solid;
			color: $primary-color;
			font-size: 15px;
			letter-spacing: 1px;
			font-weight: 700;
			line-height: 36px;
			i{
				padding-left: 8px;
				color: $primary-color;
			}
		}
	}
}
.dealSlider .imageBox:hover{
	.productDeal{
		.rating{
			color: $primary-color;
		}
	}
	.productCaption{
		.btn-border{
			background-color: $primary-color;
			color: $white-color;
			i{
				color: $white-color;
			}
		}
	}
	.productImage img{
		@include transform(scale(1.2));
	}
}

/*=== 3.3.2 FEATURE PRODUCT SECTION  ===*/
.version3.featuredProducts{
	margin-bottom:40px;
}
.tabCommon {
	.nav.nav-tabs {
		flex-direction: row;
		justify-content: center;
		border-bottom: 3px solid #ddd;
		@include tablet {
			justify-content: flex-start;
		}
		li {
			display: inline-block;
			margin-left: 12px;
			margin-right: 12px;
			@include tablet {
				margin-left: 0px;
				margin-right: 20px;
			}
			a {
				color: #252525;
				display: block;
				padding: 10px 0;
				font-size: 14px;
				text-transform: uppercase;
				font-weight: 700;
				position: relative;
				@include tablet {
					font-size: 16px;
				}
				&.active:before {
					content: '';
					width: 100%;
					height: 3px;
					bottom: -3px;
					left: 0;
					position: absolute;
					background: $primary-color;
				}
			}
		}
	}
}
